body {
  text-align: center;
  padding-top: 50px;
}

select {
  width: 200px;
  height: 30px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #fff;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: auto;
  animation: rotation 1s linear infinite;
  display: block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #ff3d00;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

img {
  max-width: 30%;
  max-height: 30%;
  width: auto;
  height: auto;
  object-fit: contain;
}

.error {
  color: red;
}

